import React from 'react'
import { useEffect } from 'react';
import Typed from "typed.js";

function About() {

    useEffect(() => {
        const typed = new Typed(".typingMail", {
          strings: ["Email Me" , "Roshish152002@gmail.com"], // Strings to display
          // Speed settings, try diffrent values untill you get good results
          typeSpeed: 100,
          backSpeed: 50,
          showCursor: false,
          });

        return () => {
            typed.destroy();
          };
        }, []);

    return (
        <div>
            <span className="typingMail"></span>
        </div>
    )
}

export default About
