import './HeroSection.css'
import Typed from "typed.js";
import React, { useEffect, Suspense } from "react";
import { Link } from 'react-router-dom';
import bg1 from "../images/bg4.jpg";

function Home() {
    useEffect(() => {
        const typed = new Typed(".typing1", {
          strings: ["नमस्ते,", " Hello,", "  Hola,", " Bojour," , "こんにちわ," , "Olá," , "Dzień dobry," , "प्रणाम," , "你好," , "Hallo," , "γεια σας,"], // Strings to display
          // Speed settings, try diffrent values untill you get good results
          typeSpeed: 100,
          backSpeed: 100,
          loop: true
        });

        return () => {
            typed.destroy();
          };
        }, []);
        
    return (
        <div className='container'>
            <div className="bgimageMain">
                <Suspense fallback={<div />}>
                    <img src={bg1} alt=""/>
                </Suspense>
            </div>
            <p className='main'>
                <span className="typing1"></span>
            </p>
            < Link to = '/about' style={{textDecoration: 'none'}}>
            <h2 className='body'>My name is Roshish Shrestha.</h2>
            </Link>
            <div className='Socials'>
                <div className='facebook'>
                   <i class="fab fa-facebook-f fa-2x" onClick={()=> window.open("https://www.facebook.com/roshish.shrestha", "_blank")}></i>
                </div>
                <div className='instagram'>
               <i class="fab fa-instagram fa-2x" onClick={()=> window.open("https://www.instagram.com/roshish.s", "_blank")}></i>
                </div>
                <div className='youtube'>
                    <i class="fab fa-youtube fa-2x" onClick={()=> window.open("https://www.youtube.com/Playedix", "_blank")}></i>
                </div>
                <div className='github'>
                    <i class="fab fa-github fa-2x" onClick={()=> window.open("https://github.com/R0shish", "_blank")}></i>
                </div>
            </div>
        </div>
    )
}

export default Home;
