import React , { useState, Suspense } from 'react';
import TypingMail from '../typingMail'
import styled , { keyframes } from 'styled-components'
import Background from '../../images/work.jpg'
import './Work.css'

function Work() {

      const[click, setClick] = useState(false);

      const translate = keyframes`
        from {
          transform: translate(0,0);
          color: white;
        }

        to {
          transform: translate(0, -25%) scale(1.1);
          color:lightcoral;
        }
      `;

      const Translate = styled.div`
        animation: ${translate} .6s linear ;
        animation-fill-mode: forwards;
      `;

      const Color = styled.div`
        color:white;
      `;
      
      const translateMail = keyframes`
        from {
          transform: translate(0,0);
          color: white;
        }

        to {
          transform: translate(0, -100%) scale(1);
          color:black;
        }
      `;

      const TranslateMail = styled.div`
        animation: ${translateMail} .6s linear ;
        animation-fill-mode: forwards;
      `;

      
      return (
          <div className='container'>
            <div className="bgimage">
              <Suspense fallback={<div />}>
                <img src={Background} alt=""/>
              </Suspense>
            </div>
              {click ? <Translate><h1 className='Header' onClick={() => setClick(true) } >Help me fill this page? 🥹</h1></Translate> : <Color><h1 className='Header' onClick={() => setClick(true)}>Help me fill this page? ◀</h1></Color>}
                  <TranslateMail>{click && <div className='mail'><a href="mailto:roshish152002@gmail.com" target="_blank" rel="noopener noreferrer"><TypingMail /></a></div>}</TranslateMail>
          </div>
      );
      }
export default Work

